<template>
  <!-- 计划详情 -->
  <div class="plan_box">
    <div class="left">
      <div class="contain">
        <div class="item_header">基本信息</div>
        <div class="item_two">
          <div class="code">
            <small>计划编号</small>
            <div class="code_box">
              <el-input
                size="mini"
                v-model="plan_number"
                placeholder=""
              ></el-input>
            </div>
          </div>
          <div class="code">
            <small>计划主题</small>
            <div class="code_box">
              <el-input
                size="mini"
                v-model="plan_subject"
                placeholder=""
              ></el-input>
            </div>
          </div>
        </div>
        <div class="item_two">
          <div class="code">
            <small>计划维度</small>
            <div class="code_box">
              <el-select
                size="mini"
                placeholder=""
                class="newselect"
                v-model="planningDimensionId"
              >
                <el-option
                  v-for="item in planningDimensionlist"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </div>
          </div>
          <div class="code">
            <small>计划时段</small>
            <div class="code_box">
              <el-select
                size="mini"
                placeholder=""
                class="newselect"
                v-model="plan_dateId"
              >
                <el-option
                  v-for="item in plan_datelist"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </div>
          </div>
        </div>

        <div class="item_two">
          <div class="code">
            <small>备注</small>
            <div class="code_box">
              <el-input size="mini" v-model="remarks" type="textarea" :rows="1"></el-input>
            </div>
          </div>
          <div class="code">
            <small>计划人</small>
            <div class="code_box">
              <el-select
                size="mini"
                placeholder=""
                class="newselect"
                v-model="plan_empId"
              >
                <el-option
                  v-for="item in plan_emplist"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </div>
          </div>
        </div>

        <div class="item_two">
          <div class="code">
            <small>计划时间</small>
            <div class="code_box">
              <el-date-picker
              style="width: 100%"
              size="mini"
              v-model="plannedTime"
              type="date"
              placeholder="选择日期"
            >
            </el-date-picker>
            </div>
          </div>
          <div class="code">
            <small>创建人</small>
            <div class="code_box">张三</div>
          </div>
        </div>
        <div class="item_two">
          <div class="code">
            <small>创建时间</small>
            <div class="code_box">
              <small> 2021-09-09 13:22</small>
            </div>
          </div>
          <div class="code">
            <small>审批状态</small>
            <div class="code_box"><small>已通过</small></div>
          </div>
        </div>
        <div class="item_two">
          <div class="code">
            <small>审批人</small>
            <div class="code_box"><small>张三</small></div>
          </div>
          <div class="code">
            <small>审批时间</small>
            <div class="code_box">
              <small> 2021-09-09 13:22</small>
            </div>
          </div>
        </div>
        <div class="item_two line_box">
          <div class="code">
            <small>审批备注</small>
            <div class="code_box">--</div>
          </div>
        </div>
      </div>
      <div class="footer">
        <div class="footer_title">
          <p>计划明细</p>
          <img src="../../image/new.png" alt="" @click="reset()" />
        </div>
        <div class="power_main">
          <el-table
            :data="tableData"
            border
            style="width: 100%"
            size="mini"
            :height="$store.state.innerheight - 520"
            :header-cell-style="{ background: '#f5f5f5' }"
          >
            <el-table-column fixed type="index" label="序号" width="80">
            </el-table-column>
            <el-table-column prop="plan_business" label="计划业务" width="">
            </el-table-column>
            <el-table-column prop="plan_num" label="计划数量/数额" width="">
            </el-table-column>
            <el-table-column prop="plan_yes" label="已完成" width="">
            </el-table-column>
            <el-table-column prop="plan_rate" label="进度" width="">
              <template #default="scope">
                <el-progress
                  class="elprogress"
                  width="35"
                  type="circle"
                  :percentage="scope.row.plan_rate"
                ></el-progress>
              </template>
            </el-table-column>
            <el-table-column label="操作" width="">
              <template #default="scope">
                <el-button
                  @click="handleEdit(scope.row)"
                  type="text"
                  size="mini"
                  >编辑</el-button
                >
                <el-button
                  style="color: red"
                  type="text"
                  size="mini"
                  @click="handleDel(scope.row)"
                  >移除</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
    <div class="right" v-if="isedit==1">
      <div class="item_header">主管评论</div>
      <div>
        <el-timeline>
          <el-timeline-item
            v-for="(activity, index) in activities"
            :key="index"
            type="primary"
          >
            <div class="right_item">
              <div>{{ activity.content }}</div>
              <div class="logo"><img src="../../image/del.png" alt="" /></div>
            </div>
            <div>
              <div class="right_item1">
                {{ activity.name }} {{ activity.timestamp }}
              </div>
            </div>
          </el-timeline-item>
        </el-timeline>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs } from "vue";
import { getCurrentInstance } from "@vue/runtime-core";
export default {
  setup() {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      isedit:null,
      activities: [
        {
          content: "计划设置稍微高",
          name: "王主管",
          timestamp: "2021-09-04 12:33",
        },
        {
          content: "计划设置的太低",
          name: "王主管",
          timestamp: "2021-09-01 12:33",
        },
      ],
      planningDimensionlist: [],
      planningDimensionId: "",
      plan_dateId: "",
      plan_datelist: [],
      plan_number: "",
      plan_subject: "",
      plan_empId: "",
      plan_emplist: [],
      plannedTime: "",
      report_date: "",
      job_content: "",
      remarks: "",
      tableData: [
        {
          plan_business: "",
          plan_num: "",
          plan_yes: "",
          plan_rate: "",
        },
      ],
    });
    const reset = () => {
      proxy.tableData.push({
        index: proxy.tableData.length,
        plan_business: "",
        plan_num: "",
        plan_yes: "",
        plan_rate: "",
      });
    };
    const changeedit = (n) => {
      proxy.isedit = n
    }
    const handleEdit = () => {};
    const handleDel = () => {};
    return {
      ...toRefs(state),
      handleEdit,
      handleDel,
      reset,
      changeedit
    };
  },
};
</script>

