<template>
  <!-- 计划信息 -->
  <div class="container">
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="计划信息" name="planInformation">
        <planInformationDetails ref="planInformationDetails"></planInformationDetails>
      </el-tab-pane>
    </el-tabs>
    <div class="loginOut">
      <el-form-item>
        <el-button
           @click="examine"
          type="primary"
          size="mini"
          v-if="isedit==1"
          >审批</el-button
        >
        <el-button
          type="primary"
          size="mini"
          @click="saveBtn()"
          >保存</el-button
        >
        <el-button size="mini" @click="returnBtn()"
          >返回</el-button
        >
      </el-form-item>
    </div>
    <examine ref="examine"></examine>
  </div>
</template>

<script>
import { reactive, toRefs } from "vue";
import planInformationDetails from "../plan_management/planInformationDetails.vue";
import examine from '../plan_management/examinealert.vue'
import { getCurrentInstance, onMounted } from "@vue/runtime-core";
export default {
  components: {
    planInformationDetails,
    examine
  },
  setup() {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      activeName: "planInformation",
      isedit:null
    });
    const saveBtn = () => {
      proxy.$router.push("/plan");
    };
    const returnBtn = () => {
      proxy.$router.go(-1);
    };
    const examine = () => {
            proxy.$refs.examine.changestatus()
        }
        onMounted(()=>{
          proxy.isedit = proxy.$route.query.isedit
          proxy.$refs.planInformationDetails.changeedit(proxy.isedit)
        })
    return {
      ...toRefs(state),
      saveBtn,
      returnBtn,
      examine
    };
  },
};
</script>

<style scoped>
.container {
  background-color: #fff;
}

.loginOut {
  position: absolute;
  right: 35px;
  top: 60px;
}
</style>
<style>
.el-tabs__header {
  margin: 0;
}
</style>